import { queryCollectByTypeRequest, queryCollectByLabelRequest } from '@/network/collection.js'
import { formatTimeStamp } from '@/common/index.js'

import { mapState } from 'vuex'

export default {
	data() {
		return {
			//获取的搜索列表数据
			searchList: []
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		
		//按文件类型搜索
		queryCollectByType(type) {
			const reqStr = `?account=${this.userInfo.user.userAccount}&collectType=${type}`
			queryCollectByTypeRequest(reqStr).then(res => {
				console.log(res)
				if(res.status === 200) {
					if(res.data.code === 2000) {
						this.searchList = res.data.data
					}else if(res.data.code === 4004) {
						uni.showToast({
							title: '您无此类型收藏',
							icon: 'none'
						})
					}else {
						uni.showToast({
							title: '程序走丢了,请稍后重试',
							icon: 'none'
						})
					}
				}else if(res.status === 404) {
					uni.showToast({
						title: '网络走丢了,请稍后重试',
						icon: 'none'
					})
				}else {
					uni.showToast({
						title: '程序走丢了,请稍后重试',
						icon: 'none'
					})
				}
			}).catch(err => {
				console.log(err)
				uni.showToast({
					title: '程序走丢了,请稍后重试',
					icon: 'none'
				})
			})
		},
		
		//按标签搜索
		queryCollectByLabel(type) {
			const reqStr = `?account=${this.userInfo.user.userAccount}&collectLabel=${type}`
			queryCollectByLabelRequest(reqStr).then(res => {
				console.log(res)
				if(res.status === 200) {
					if(res.data.code === 2000) {
						this.searchList = res.data.data
					}else if(res.data.code === 4004) {
						
					}else {
						uni.showToast({
							title: '程序走丢了,请稍后重试',
							icon: 'none'
						})
					}
				}else if(res.status === 404) {
					uni.showToast({
						title: '网络走丢了,请稍后重试',
						icon: 'none'
					})
				}else {
					uni.showToast({
						title: '程序走丢了,请稍后重试',
						icon: 'none'
					})
				}
			}).catch(err => {
				console.log(err)
				uni.showToast({
					title: '程序走丢了,请稍后重试',
					icon: 'none'
				})
			})
		},
		//跳转外链
		toChain(link) {
			uni.navigateTo({
				url: `/components/content/collection/collectionCpns/CollectChain?link=${link}&name=img-webview`
			})
		},
	},
	filters: {
		getFormatTime(timeStamp) {
			return formatTimeStamp(timeStamp, 'yyyy-MM-dd hh:mm')
		}
	},
	onLoad(option) {
		console.log(option)
		if(option.cate === 'type') {
			this.queryCollectByType(option.type)
		}else if(option.cate === 'label') {
			this.queryCollectByLabel(option.type)
		}
	}
}
